import React, { useState, useRef, useLayoutEffect } from "react";
import BubbleUI, { defaultOptions } from "react-bubble-ui";
import "react-bubble-ui/dist/index.css";
import companyData from "./companies";
import CompanyBubble from "./CompanyBubble";



export default function App(props) {

  const getStockBubbles = () => {
    return companyData.slice(0, 20).map((company, i) => {
      return <CompanyBubble {...company} key={i} />;
    });
  };
  const stockBubbles = getStockBubbles();

  const [options, setOptions] = useState({
    size: 180,
    minSize: 20,
    gutter: 8,
    provideProps: true,
    numCols: 6,
    fringeWidth: 160,
    yRadius: 130,
    xRadius: 220,
    cornerRadius: 50,
    showGuides: false,
    compact: true,
    gravitation: 5,
  });

  const scrollToRef = (ref) => {
    if (ref) {
      const y = ref.current.offsetTop - 50;
      window.scrollTo(0, y);
    } else {
      window.scrollTo(0, 0);
    }
  };

  useLayoutEffect(() => {
    const hash = window.location.hash.split("#/")[1];
    if (hash == "demo") {
      scrollToRef(demoRef);
    } else if (hash == "docs") {
      scrollToRef(docsRef);
    } else if (hash == "code") {
      scrollToRef(codeRef);
    } else if (hash == "layout") {
      scrollToRef(layoutRef);
    } else if (hash == "style") {
      scrollToRef(styleRef);
    }
  }, []);

  const demoRef = useRef(null);
  const docsRef = useRef(null);
  const codeRef = useRef(null);
  const layoutRef = useRef(null);
  const styleRef = useRef(null);

  return (
    <React.Fragment>    
      <BubbleUI className="bubbleUI" options={options}>
        {stockBubbles}
      </BubbleUI>
    </React.Fragment>
  );
}

